var server = {};
$.ajax({
    url: 'static/config.json',
    async: false,
    dataType: 'json',
    isLocal: true,
    error: function (e) {
        console.error(e);
    },
    success: function (response) {
        server = response[response.use];
    }
});

export const serverUrl = server.protocol + "://" + server.host + ":" + server.port + "/" ;


